input {
    padding: 10px;
    margin: 10px 0px;
    border-radius: 5px;
    border: 1px solid grey;
}

.formInput {
    display: flex;
    flex-direction: column;
    width: 260px;
}

label{
    font-size: 12px;
    color: grey;
}


 span{
    font-size:12px;
    padding:2px;
    color:red;
    display:none;
  
 }

 input:invalid[focused="true"]{
    border:1px solid red;
 }

 input:invalid[focused="true"]~span{
    display: block;
 }